.rating {
  float:left;
  margin-bottom: 100px;
}

.rating input {
  display: none;
}

/* :not(:checked) is a filter, so that browsers that don’t support :checked don’t 
 follow these rules. Every browser that supports :checked also supports :not(), so
 it doesn’t make the test unnecessarily selective */
.rating:not(:checked) > input {
  position:absolute;
  top:-9999px;
  clip:rect(0,0,0,0);
}

.rating:not(:checked) > label {
  float:right;
  width:3em;
  padding:0 .1em;
  overflow:hidden;
  white-space:nowrap;
  cursor:pointer;
  font-size:200%;
  line-height:1.2;
  color:#ddd;
}

.rating:not(:checked) > label:before {
  content: '★ ';
}

.rating > input:checked ~ label {
  color: gold;
}

.rating:not(:checked) > label:hover,
.rating:not(:checked) > label:hover ~ label {
  color: gold;
}

.rating > input:checked + label:hover,
.rating > input:checked + label:hover ~ label,
.rating > input:checked ~ label:hover,
.rating > input:checked ~ label:hover ~ label,
.rating > label:hover ~ input:checked ~ label {
  color: gold;
}

.rating > label:active {
  position:relative;
  top:2px;
  left:2px;
}


.your-rating {
  margin-top: 30px;
  background-color: #ffffff;
  padding: 10%;
  width: 400px;
  height: 200px;
  text-align: center;
  overflow: scroll;
}


.movie-img img{
  height: 360px;
  width: auto;
}

.movie-img{
  height: 360px;
  width: 260px;
  overflow: scroll;
}

.search-content{
  margin-top: 0;
}
